.banner-left {
  position: relative;
  top: -120px;
  z-index: 3;
  width: 610px;
}

.banner-middle {
  z-index: 0;
  position: absolute;
  top: 25px;
  right: 180px;
}

.banner-middle .game {
  z-index: 1;
  width: 200px;
  height: 160px;
  position: absolute;
  top: 220px;
  left: 245px;
  overflow: hidden;
}

.banner-middle .game-item {
  position: relative;
  top: -60px;
  width: 33.33%;
}

.banner-middle .game-item img {
  width: 80%;
  -webkit-box-shadow: 0px 7px 0px 0px rgba(103, 212, 224, 0.7);
  -moz-box-shadow: 0px 7px 0px 0px rgba(103, 212, 224, 0.7);
  box-shadow: 0px 7px 0px 0px rgba(103, 212, 224, 0.7);
}

.banner-middle .game #html {
  animation-name: game;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

.banner-middle .game #css {
  animation-name: game;
  animation-duration: 6s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

.banner-middle .game #js {
  animation-name: game;
  animation-duration: 6s;
  animation-delay: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

@keyframes game {
  from {
    top: -60px;
  }

  to {
    top: 150px;
  }
}

.banner-middle img {
  width: 730px;
  -webkit-box-shadow: 0px 6px 17px 1px #67d4e0;
  -moz-box-shadow: 0px 6px 17px 1px #67d4e0;
  box-shadow: 0px 6px 17px 1px #67d4e0;
  filter: brightness(0.85);
}

